import { useEffect, useMemo, useRef, useState } from 'react'
import {
  EventApi,
  DateSelectArg,
  EventClickArg,
  EventContentArg,
  EventChangeArg
} from '@fullcalendar/core'
import Sortable from 'sortablejs'
import { Card, Input, InputRef } from 'antd'
import zhLocale from '@fullcalendar/core/locales/zh-cn'
import FullCalendar from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import './index.less'
import Panl from '@/components/TypingCard'
import { Color, TaskList } from '@/Type'
import CollCard from '@/components/CollCard'
import { useRecoilState } from 'recoil'
import { calendar } from '@/store/Module/user'

const { Search } = Input

const recursiveQuery = (e): Date => {
  if ([...e.classList].includes('fc-daygrid-day')) {
    return new Date(e.getAttribute('data-date'))
  } else {
    return recursiveQuery(e.parentElement)
  }
}
const Calendar = () => {
  const [taskList, setTaskList] = useState([])
  const [currentColor, setCurrentColor] = useState<Color>({
    color: '#007bff',
    name: 'health',
    describe: ''
  })
  const searchBox = useRef<InputRef>(null)
  const [events, setEvents] = useRecoilState<EventApi[]>(calendar) // 
  const [dragging, setDragging] = useState<any>({}) // 
  const [selectEve, setSelectEve] = useState<DateSelectArg>() // 
  const [dstDom, setDstDom] = useState() // dom
  const calendarRef = useRef(null)

  const handleDragOnEnd = (event: any) => {
    const dstDom = event.originalEvent // dom
    setDstDom(dstDom)
  }

  const handleDragOnStart = (event: any) => {
    const srcDom = event.item // dom
    const dra = { value: srcDom.innerHTML, color: srcDom.style.backgroundColor }
    setDragging(dra)
  }

  useEffect(() => {
    if (dstDom) {
      handleDrop(dstDom)
    }
  }, [dstDom])
  useEffect(() => {
    const sortable = document.getElementById('sortable')
    new Sortable(sortable, {
      animation: 150,
      forceFallback: true,
      fallbackClass: 'dragged-item',
      onEnd: handleDragOnEnd,
      onStart: handleDragOnStart
    })
    colorClick(currentColor)
  }, [])

  // 
  const handleDrop = (e) => {
    try {
      e.preventDefault()
      const id = new Date().toISOString()
      const ev: EventApi = {
        id,
        title: dragging.value,
        backgroundColor: dragging.color,
        borderColor: dragging.color
      }
      if (selectEve) {
        ev.start = selectEve.startStr
        ev.end = selectEve.endStr
        ev.allDay = selectEve.allDay
        setSelectEve(null)
      } else {
        ev.start = recursiveQuery(e.target)
        ev.allDay = true
      }
      if (dragging) {
        setEvents([...events, ev])
        // setDragging(null)
      }
    } catch (e) {
      console.log(e)
    }
  }
  const [colorList] = useState<Array<Color>>([
    {
      color: '#007bff',
      name: 'health',
      describe: ''
    },
    {
      color: '#ffc107',
      name: 'same',
      describe: ''
    },
    {
      color: '#dc3545',
      name: 'warn',
      describe: ''
    },
    {
      color: '#6c757d',
      name: 'ordinary',
      describe: ''
    }
  ])
  // 
  const onSearch = (item: string) => {
    if (!item) return
    setTaskList([...taskList, { ...currentColor, value: item }])
  }
  // 
  const colorClick = (item: Color) => {
    setCurrentColor(item)
    const child: any = searchBox?.current?.input?.parentNode?.nextSibling?.firstChild
    child.style.backgroundColor = item.color
    const span: any = searchBox?.current?.input?.parentNode
    span.style.borderColor = item.color
  }

  // 
  const handleSelect = (selectInfo: DateSelectArg) => {
    setSelectEve(selectInfo)
    const calendarApi = selectInfo.view.calendar
    // calendarApi.unselect() // 
    // calendarApi.addEvent({
    //   id: Math.random() + '',
    //   title: '',
    //   start: selectInfo.startStr,
    //   end: selectInfo.endStr,
    //   allDay: selectInfo.allDay,
    //   classNames: ['ggg']
    // })
  }
  // 
  const renderEventContent = (eventContent: EventContentArg) => {
    return (
      <div
        style={{
          backgroundColor: eventContent.backgroundColor,
          width: '100%',
          borderRadius: '3px',
          color: '#fff'
        }}>
        <b>{eventContent.timeText}</b>
        &nbsp;
        <i>{eventContent.event.title}</i>
      </div>
    )
  }

  // 
  const handleEventClick = (clickInfo: EventClickArg) => {
    // clickInfo.event.remove()
    // setEvents(events.filter((item) => item.id !== clickInfo.event.id))
  }
  //  -- 
  const eventChange = (event: EventChangeArg) => {
    const arr = events.map((item) => {
      if (item.id === event.event.id) {
        item.start = event.event.start
        return item
      } else {
        return item
      }
    })
    setEvents(arr)
  }

  return (
    <Panl title="" source={''} className="calendar">
      <div className="eventList" style={{ position: 'sticky', top: '10px' }}>
        <CollCard title="" style={{ width: 300 }}>
          <div id="sortable">
            {taskList.map((item) => {
              return (
                <p key={item.color} className="task" style={{ backgroundColor: item.color }}>
                  {item.value}
                </p>
              )
            })}
          </div>
        </CollCard>
        {/* <Card title="" bordered={false} style={{ width: 300 }}>
          <div onDrop={handleDelDrop} onDragOver={(e) => e.preventDefault()}>
            
          </div>
        </Card> */}
        <Card title="" bordered={false} style={{ width: 300 }}>
          <div className="icon-block">
            {colorList.map((item) => {
              return (
                <p
                  onClick={() => colorClick(item)}
                  key={item.name}
                  className="son"
                  style={{ backgroundColor: item.color }}></p>
              )
            })}
          </div>
          <Search
            ref={searchBox}
            className={currentColor.name}
            placeholder=""
            allowClear
            enterButton=""
            onSearch={onSearch}
          />
        </Card>
      </div>
      <div id="sortable2" className="app-card demo-app-main">
        <FullCalendar
          ref={calendarRef}
          plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
          headerToolbar={{
            left: 'prev,next today',
            center: 'title',
            right: 'dayGridMonth,timeGridWeek,timeGridDay'
          }}
          initialView="dayGridMonth"
          editable={true}
          selectable={true}
          selectMirror={true}
          dayMaxEvents={true}
          locale={zhLocale}
          events={events}
          eventContent={renderEventContent} // custom render function
          eventClick={handleEventClick}
          select={handleSelect}
          eventChange={eventChange} // 
          // eventReceive={eventChange} // 
          // weekends={weekendsVisible}
          // initialEvents={INITIAL_EVENTS} // alternatively, use the `events` setting to fetch from a feed
          // select={handleDateSelect}
          // eventContent={renderEventContent} // custom render function
          // eventClick={handleEventClick}
          // eventsSet={handleEvents} //   events={events} 
          /* you can update a remote database when these fire:
            eventAdd={function(){}}
            eventChange={function(){}}
            eventRemove={function(){}}
            */
        />
      </div>
    </Panl>
  )
}

export default Calendar
